.custom-checkbox input {
  position: absolute;
  opacity: 0;
  cursor: pointer;
  height: 0;
  width: 0;
}

.custom-checkbox .checkmark {
  position: relative;
  height: 20px;
  width: 20px;
  background-color: #fff;
  border: 2px solid var(--primary-color, #020100);
  border-radius: 4px;
  transition: all 0.2s ease-in-out;
}

.custom-checkbox:hover input ~ .checkmark {
  background-color: var(--close-background-color, #eeeeee);
